
import api from '@/utils/api'
import { getHashParameter } from '@/utils/utils'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

import MySearch from '@/components/MySearch'
import PostList from '@/components/PostList'

export default {
name: 'PostListPage',
components: {
Tab,
TabItem,
MySearch,
PostList,
Loading
},
data () {
return {
searchTitle: '',
selected: 0,
parentId: '', // 父栏目id
currentId: '', // 当前栏目id
channelList: [],
listData: [],
loading: true,
page: 1
}
},
methods: {
getParentId () {
this.parentId = getHashParameter('cid') || '0'
if (getHashParameter('id')) {
this.currentId = getHashParameter('id')
}
},
setData (data = []) {
this.listData.splice(0)
this.listData = data
},
async loadMore (data = []) {
this.listData = this.listData.concat(data)
},
async handleSearch (value) {
this.searchTitle = value
this.page = 1
this.fetchData()
},
async onItemClick (id) {
this.currentId = id
this.page = 1
location.hash = `#cid=${this.parentId}&id=${id}`
await this.fetchData()
this.$nextTick(() => {
this.$children[3].$refs.infiniteLoading.stateChanger.reset()
})
},
async getChannelList () {
this.channelList = await api.getChannel(this.parentId)
if (!this.currentId && this.currentId !== 0) {
this.currentId = this.channelList[0].id
}
},
async fetchMoreData () {
try {
return await api.getNewsList(this.currentId, this.page++, this.searchTitle)
} catch (err) {
console.error(err)
}
},
async fetchData () {
this.loading = true

const cid = this.currentId || this.channelList[0].id

try {
const data = await api.getNewsList(cid, this.page++, this.searchTitle)
this.setData(data)
} catch (err) {
console.error(err)
}
this.loading = false
}
},
async created () {
this.getParentId()
await this.getChannelList()
this.fetchData()
}
}



