import { getLimitTitle } from '@/utils/utils'

export default {
  name: 'ItemNormal',
  props: {
    article: Object
  },
  computed: {
    imgs () {
      return this.article.imgs.split(',') || []
    },
    showTitle () {
      return getLimitTitle(this.article.title, 30)
    }
  }
}



