
import dateFormat from 'vux/src/tools/date/format.js'

import { formatLink } from '@/utils/utils'
import InfiniteLoading from 'vue-infinite-loading'
import ItemNormal from '@/components/ItemNormal'
import ItemCircle from '@/components/ItemCircle'
import ItemFocus from '@/components/ItemFocus'
import pageConfig from '@/utils/pageConfig'

export default {
name: 'PostList',
props: {
listData: {
type: Array,
default: []
},
fetchMoreData: {
type: Function
}
},
components: {
InfiniteLoading,
ItemNormal,
ItemCircle,
ItemFocus
},
data () {
return {
newsDetailsLink: pageConfig.newsDetailsLink,
postType: 'normal'
}
},
computed: {
postList () {
if (!this.listData) {
return []
}
return this.listData.map(x => {
x.showTime = dateFormat(x.publishtime * 1000, 'YYYY-MM-DD')

x.detailLink = formatLink(x.link) || `${pageConfig.newsDetailsLink}?aid=${x.id}`
return x
})
}
},
methods: {
async infiniteHandler ($state) {
const data = await this.fetchMoreData()

if (data.length > 0) {
this.$emit('infinite', data)
if (data.length < 10) {
$state.loaded()
} else {
$state.complete()
}
} else {
$state.complete()
}
}
}
}



