
import Search from 'vux/src/components/search/index.vue'


export default {
name: 'MySearch',
components: {
Search
},
data () {
return {
value: ''
}
},
methods: {
onSubmit () {
this.$refs.search.setBlur()
this.$emit('search', this.value)
},
onCancel () {
this.$refs.search.setBlur()
this.$emit('search')
},
onChange () {

}
}
}



