import dateFormat from 'vux/src/tools/date/format.js'


export const formatDate = t => {
return dateFormat(t * 1000, 'YYYY-MM-DD')
}

export const getTimeRange = (startTime, endTime) => {
let start = formatDate(startTime)
let end = formatDate(endTime)

if (start === end) {
start = dateFormat(startTime * 1000, 'YYYY-MM-DD HH:mm')
end = dateFormat(endTime * 1000, 'HH:mm')
}
return `${start} - ${end}`
}

export const formatLink = link => {
if (!/^http/.test(link)) {
link = 'http://' + link
}
return link
}

export const getLimitTitle = (title = '', limit = 18) => {
return title.length > limit ? `${title.substr(0, limit)}...` : title
}

function getHashParameters () {
var arr = (location.hash || '').split('#')[1].split('&')
var params = {}
for (var i = 0; i < arr.length; i++) {
var data = arr[i].split('=')
if (data.length === 2) {
params[data[0]] = data[1]
}
}
return params
}

export function getHashParameter (key) {
var params = getHashParameters()
return params[key]
}



